import WhiteLabelledDashboard from "./white-labelled-dashboard";

async function embedDashboard() {
  if (!document.querySelector("#img_dash_root")) {
    let dashboard = await waitForElement("#dashboard");
    let container = document.createElement("div");
    container.id = "img_dash_root";
    dashboard.prepend(container);
    log("Rendering Dashboard 2");
    const root = ReactDOM.createRoot(document.getElementById("img_dash_root"));
    root.render(<WhiteLabelledDashboard />);
    dashboard.prepend;
  }
}

export default embedDashboard;
